ght: 600;\n}\n@font-face {\n  font-family: 'Apercu';\n  src: url('/fonts/Apercu-Bold.woff2') format('woff2'),\n       url('/fons/Apercu-Bold.woff') format('woff');\n       font-weight: 700;\n}\n\n:root {\n  --ease-in-quad: cubic-bezier(0.55, 0.085, 0.68, 0.53);\n  --ease-in-quart: cubic-bezier(0.895, 0.03, 0.685, 0.22);\n  --ease-out-quad: cubic-bezier(0.25, 0.46, 0.45, 0.94);\n  --ease-out-quart: cubic-bezier(0.165, 0.84, 0.44, 1);\n  --ease-in-out-quad: cubic-bezier(0.455, 0.03, 0.515, 0.955);\n  --ease-in-out-quart: cubic-bezier(0.77, 0, 0.175, 1);\n}\n\n*,\n*:after,\n*:before {\n  -webkit-box-sizing: inherit;\n  box-sizing: inherit;\n  margin: 0;\n  padding: 0;\n  font-size: inherit;\n}\n\n:root {\n  -ms-overflow-style: -ms-autohiding-scrollbar;\n  -webkit-box-sizing: border-box;\n  box-sizing: border-box;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  text-rendering: optimizeLegibility;\n  cursor: default;\n  font-size: 0.625rem;\n  line-height: 1.4;\n}\n\nbody {\n  font-family:  \"Ceaper Round\" , sans-serif;\n  font-size: 1.6rem;\n  margin: 0;\n  font-weight: 400;\n  height: 100%;\n  padding: 0;\n  font-kerning: normal;\n}\n\na,\nbutton {\n  text-decoration: none;\n  cursor: pointer;\n}\n\na:focus {\n  outline: none;\n}\n\naudio,\ncanvas,\niframe,\nimg,\nsvg,\nvideo {\n  vertical-align: middle;\n}\n\nbutton,\ninput,\nselect,\ntextarea {\n  font-family:  \"Ceaper Round\" , sans-serif;\n  color: inherit;\n  font-family: inherit;\n  font-style: inherit;\n  font-weight: inherit;\n  border: none;\n  -webkit-appearance: none;\n}\n\ncode,\nkbd,\npre,\nsamp {\n  font-family: monospace;\n}\n\nbutton,\nfieldset {\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n  border: none;\n  outline: none;\n  background: transparent;\n}\n\ntable {\n  border-collapse: separate;\n  border-spacing: 0;\n}\n\naudio:not([controls]) {\n  display: none;\n}\n\ndetails {\n  display: block;\n}\n\ninput {\n  &:active,\n  &:focus {\n    outline: none;\n  }\n\n  &[type=\"number\"] {\n    width: auto;\n  }\n}\n\nimg.Image__Zoom ~ div {\n  background: transparent !important;\n}\n\n.Image__Small {\n  width: 100%;\n  max-width: 680px;\n  margin-left: auto;\n  margin-right: auto;\n  margin-bottom: 35px;\n\n  img {\n    width: 100%;\n  }\n}\n\n.Image__medium {\n  width: 100%;\n  margin-left: auto;\n  margin-right: auto;\n  margin-bottom: 35px;\n\n  img {\n    width: 100%;\n  }\n}\n\n.Image__large {\n  width: 100%;\n  margin-bottom: 35px;\n\n  img {\n    width: 100vw;\n    position: relative;\n    left: 50%;\n    right: 50%;\n    margin-left: -50vw;\n    margin-right: -50vw;\n  }\n}\n\n.author-alc-section {\n  padding-left: 0;\n  padding-right: 0;\n}\n\n.icon-image {\n  path {\n    fill: var(--primary);\n  }\n}\n\n@-webkit-keyframes image-load-in {\n  0% {\n    -webkit-filter: blur(8px);\n    filter: blur(8px);\n    opacity: 0;\n  }\n\n  100% {\n    -webkit-filter: blur(0);\n    filter: blur(0);\n    opacity: 1;\n  }\n}\n@keyframes image-load-in {\n  0% {\n    -webkit-filter: blur(8px);\n    filter: blur(8px);\n    opacity: 0;\n  }\n\n  100% {\n    -webkit-filter: blur(0);\n    filter: blur(0);\n    opacity: 1;\n  }\n}\n","/*BREAKPOINTS*/\n$phone: 376px;\n$phablet: 540px;\n$tablet: 735px;\n$desktop: 1070px;\n$desktop_medium: 1280px;\n$desktop_large: 1440px;\n\nbody{\n    /*FONTS*/\n    --headings: \"Apercu\", \"Heisei Kaku Gothic\", sans-serif;\n    --paragraph: \"Ceaper Round\" , sans-serif;\n    -webkit-text-size-adjust: 100%;\n-moz-text-size-adjust: 100%;\n-ms-text-size-adjust: 100%;\ntext-size-adjust: 100%;\n-moz-font-feature-settings: \"pkna\";\n-webkit-font-feature-settings: \"pkna\";\nfont-feature-settings: \"pkna\";\n\n    /*PRIMARY*/\n    --accent: #ff5445;\n    --white: #fff;\n    --black: #231e2d;\n    --grey: #b0b0b0;\n    --articleText: #4f4b57;\n    /*SECONDAY*/\n    --cream: #e5ddd5;\n    --botticelli: #cadee5;\n    --basil: #cfe5ca;\n    /*OTHER*/\n    --background-color: #fff;\n    -